export default (Comp) => {
    function TestHocComp({ Component, pageProps, ...rest}) {
        // const name = name + '123'
        console.log(Component, pageProps)
        if(pageProps){
            pageProps.test = '123'
        }
        return <Comp Component={Component} pageProps={pageProps} {...rest} />
    }
    TestHocComp.getInitialProps = Comp.getInitialProps //if the hoc package the component, the hoc should inherit getInitialProps from the Component
    return TestHocComp
}